import { Utility } from './../../../services/utility';
import { ViewController } from 'ionic-angular';
import { NativeService } from './../../../services/native-service';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the MessageImagePickerPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-message-image-picker',
  templateUrl: 'message-image-picker.html',
})
export class MessageImagePickerPage {

  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    public nativeService: NativeService,
    public view: ViewController) {
      
  }

   
  getPictureByCamera() { 
    let param = this.navParams.data;
    if (this.nativeService.isMobile()) {
      this.nativeService.getFullPictureByCamera({ 
      }).subscribe(img => {
        this.nativeService.compressImage(img, { MaxWidthOrHeight: 1024 }).subscribe(res=>{ 
          param.callback && param.callback(param.scope, res.dataURL,'Image') 
          this.view.dismiss();
        })
        
      }, (err) => {
        this.nativeService.showToast(err)
      });
    } else {
      Utility.generateQRCode("https://iproud.fihmb.com/Resources/Widgets/66295698871296.png").then(img=>{ 
        param.callback && param.callback(param.scope, img,'Image')
        this.view.dismiss();
      })
    }
  }

  getPictureByPhotoLibrary(){ 
    let param = this.navParams.data;
    if (this.nativeService.isMobile()) {
      this.nativeService.getFullPictureByPhotoLibrary({ 
      }).subscribe(img => {
        this.nativeService.compressImage(img, { MaxWidthOrHeight: 1024 }).subscribe(res=>{ 
          param.callback && param.callback(param.scope, res.dataURL,'Image') 
          this.view.dismiss();
        }) 
      }, (err) => {
        this.nativeService.showToast(err)
      });
    } else {
      Utility.generateQRCode("https://iproud.fihmb.com/Resources/Widgets/66295698871296.png").then(img=>{ 
        param.callback && param.callback(param.scope, img,'Image')
        this.view.dismiss();
      })
    }
  }

}



